button {
    position: relative;
    border: none;
    background: transparent;
    padding: 0;
    cursor: pointer;
    outline-offset: 4px;
    transition: -webkit-filter 250ms;
    transition: filter 250ms;
    transition: filter 250ms, -webkit-filter 250ms;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    touch-action: manipulation;
}
   
.shadow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    background: hsl(0deg 0% 0% / 0.25);
    will-change: transform;
    transform: translateY(2px);
    transition: transform
       600ms
       cubic-bezier(.3, .7, .4, 1);
}
   
.edge {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    background: linear-gradient(
       to left,
       hsl(90deg 100% 16%) 0%,
       hsl(90deg 100% 32%) 8%,
       hsl(90deg 100% 32%) 92%,
       hsl(90deg 100% 16%) 100%
    );
}
   
.front {
    display: block;
    position: relative;
    padding: 12px 27px;
    border-radius: 12px;
    font-size: 1.1rem;
    color: white;
    background: hsl(90deg, 27%, 46%);
    will-change: transform;
    transform: translateY(-4px);
    transition: transform
       600ms
       cubic-bezier(.3, .7, .4, 1);
}
   
button:hover {
    -webkit-filter: brightness(110%);
            filter: brightness(110%);
}
   
button:hover .front {
    transform: translateY(-6px);
    transition: transform
       250ms
       cubic-bezier(.3, .7, .4, 1.5);
}
   
button:active .front {
    transform: translateY(-2px);
    transition: transform 34ms;
}
   
button:hover .shadow {
    transform: translateY(4px);
    transition: transform
    250ms
    cubic-bezier(.3, .7, .4, 1.5);
}
   
button:active .shadow {
    transform: translateY(1px);
    transition: transform 34ms;
}
   
button:focus:not(:focus-visible) {
    outline: none;
}
