.square {
    background: transparent;
    background-size: cover;
    border: 1px solid transparent;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 70px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center; 
    width: 70px;
}

.square:focus {
    outline: none;
}
  
